<template>
  <div>
    <ul>
      <li>{{ course.id }}</li>
      <li>{{ course.title }}</li>
      <li>{{ course.author }}</li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data() {
    return {
      course: {
        id: '',
        title: '',
        author: ''
      }
    }
  },
  created() {
    this.getCourse()
  },
  methods: {
    getCourse() {
      axios.get('/api/course')
        .then(res => {
          console.log(123)
          this.course = res.data[0]
          console.log(res.data[0].id)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style>
  li {
    font-size: 50px;
  }
</style>



