import Vue from 'vue'
import Router from 'vue-router'
// 主页部分
import Homepage from '../components/Homepage'

// 课程部分
import Course from '../components/Course'

// 个人中心部分
import Personal from '../components/Personal'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Homepage
    },
     {
      path: '/course',
      name: 'course',
      component: Course,
      children:[
        
      ]
    },
     {
      path: '/personal',
      name: 'personal',
      component: Personal
    }
  ]
})



